<template>
    <div class="content">
        <div class="main-title" v-if="repaymentUser">
            <div class="main-title__wrap main-title--nowrap">
                <div class="h1">{{ repaymentUser.name }} </div>
            </div>
            <div class="main-title__right --icon" v-if="repaymentUser">
                <span class="i i-phone"></span>
                <span class="t">{{ repaymentUser.phone }}</span>
            </div>
        </div>

        <section class="container">

            <div class="container__full">
                <div class="coupon-list" v-if="repaymentCoupons.length > 0">
                    <repayment-list-item v-for="coupon in repaymentCoupons"
                                      :item="coupon"
                                      :key="coupon.id">
                    </repayment-list-item>
                    <popup :is-set-styled="true" :show="popups.repaymentAnnul.show" :name="popups.repaymentAnnul.name">
                        <repayment-annul :coupon="currentItem"></repayment-annul>
                    </popup>


                    <popup :is-set-styled="true" :show="popups.repaymentRepay.show" :name="popups.repaymentRepay.name">
                        <repayment-repay :coupon="currentItem"></repayment-repay>
                    </popup>


                    <popup :is-set-styled="true" :show="popups.couponHistory.show" :name="popups.couponHistory.name">
                        <coupon-history :is-partner="true" :coupon="currentItem"></coupon-history>
                    </popup>
                </div>
                
                
                <div class="wrapper-content mt32" v-else>
                    <div class="sect-blank">
                        <div class="wrap text-center">
                            <loader v-if="isLoad"></loader>
                            <div class="text-size-l text-bolder text-color-grey mt16" v-else>
                                <span v-if="repaymentCouponsOther > 0">
                                    На указанный номер оформлено купонов: <br>
                                    В вашей компании — 0 шт.<br>
                                    У других поставщиков – {{ repaymentCouponsOther }} шт.</span>
                                    
                                <span v-else>На указанный номер не оформлено ни одного купона</span>
                            </div>
                        </div>
                        <div class="mini-zems"></div>
                    </div>
                    <!-- end sect-blank -->
                </div>

            </div>
            <!-- end container__full -->

        </section>
        <!-- end container -->


    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import Loader from '@/components/Loader.vue';
    import RepaymentListItem from '@/components/partner/RepaymentListItem.vue';
    import CouponHistory from '@/components/modal/CouponHistory.vue';
    import RepaymentAnnul from '@/components/modal/RepaymentAnnul.vue';
    import RepaymentRepay from '@/components/modal/RepaymentRepay.vue';
    import Popup from '@/components/modal/Popup.vue';

    export default {
        metaInfo() {
            return {
                title: 'Поиск и погашение купонов',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Repayment",
        components: {
            Loader,
            RepaymentListItem,
            CouponHistory,
            RepaymentAnnul,
            RepaymentRepay,
            Popup,
        },
        beforeDestroy(){
            this.$store.commit('partnerCoupons/setRepaymentEmpty');
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                isLoad: state =>  state.partnerCoupons.isLoad,
                currentItem: state =>  state.partnerCoupons.currentItem,
                phoneSearch: state =>  state.partnerCoupons.phoneSearch,
                repaymentCoupons: state =>  state.partnerCoupons.repaymentCoupons.items,
                repaymentCouponsOther: state =>  state.partnerCoupons.repaymentCoupons.other,
                repaymentUser: state =>  state.partnerCoupons.repaymentCoupons.user,
            }),
        },
    }
</script>

